body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main {
  margin-bottom: 10vh;
}

.Nav {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 1;
  color: #666666;
  font-size: 10px;
  background: #fff;
  border-top: 0.5px solid #dfdfdf;
}
.Nav ul {
  display: flex;
  margin: 0;
  padding: 1vh;
  list-style: none;
  justify-content: center;
}
.Nav ul li {
  flex-grow: 1;
  position: relative;
}
.Nav ul li a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  height: 8vh;
  text-decoration: none;
  background: transparent;
}
.Nav ul .unactive {
  color: gray;
}

* {
  border: none;
}
.vseller {
  width: 100%;
  height: 90vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-color: #c3ddeb;
  text-align: center;
}
.vseller .seller-container {
  width: 90%;
}
.vseller .seller-container .seller-title {
  width: 100%;
  font-size: 25px;
  font-weight: bold;
  letter-spacing: 20px;
  padding-left: 20px;
  box-sizing: border-box;
  margin-bottom: 10px;
  color: #000;
}
.vseller .seller-container .seller-text {
  width: 100%;
  font-size: 14px;
  padding: 0 0 10px 6px;
  letter-spacing: 6px;
  box-sizing: border-box;
  margin-bottom: 50px;
  color: #666;
  border-bottom: 1px solid #fff;
}
.vseller .seller-container .seller-search {
  display: flex;
  justify-content: center;
  align-items: center;
}
.vseller .seller-container .seller-search .seller-getid {
  height: 30px;
  width: 200px;
  padding: 10px 35px;
  box-sizing: border-box;
  letter-spacing: 3px;
  text-align: center;
  outline: none;
  border-radius: 50px;
  color: #888;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
.vseller .seller-container .seller-search button::after {
  border: none;
}
.vseller .seller-container .seller-search button {
  margin: 0;
  padding: 0;
  background-color: rgba(0, 0, 0, 0);
}
.vseller .seller-container .seller-search .seller-search-btn {
  width: 30px;
  height: 30px;
  position: absolute;
  margin-left: 80px;
}
.vseller .seller-container .seller-search .seller-search-btn img {
  width: 30px;
}
.vseller .seller-container .seller-search ::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #aaa;
}
.vseller .seller-container [class*="cloud"] {
  position: fixed;
  bottom: 100px;
  background-color: #fff;
  border-radius: 50%;
}
.vseller .seller-container .cloud1 {
  width: 150px;
  height: 150px;
  left: -30px;
  top: 70vh;
}
.vseller .seller-container .cloud2 {
  width: 250px;
  height: 300px;
  left: 33px;
  top: 65vh;
}
.vseller .seller-container .cloud3 {
  width: 200px;
  height: 200px;
  left: 200px;
  top: 70vh;
}

.header-new {
  background: #c8e8fc;
}
.header-new .avatar {
  border: 3px solid #fff;
  margin: 8vw;
}
.header-new .seller-name {
  display: inline;
  color: #000;
  font-weight: 700;
  letter-spacing: 5px;
}
.admin-board {
  margin: 5vw;
  text-align: center;
}
.admin-board button {
  margin: 1vw;
}
.gg {
  text-align: center;
  margin: 3vw;
  padding: 2vw;
  box-shadow: 0 5px 10px 0 rgba(174, 174, 174, 0.4);
  border-radius: 10px;
}
.gg div {
  margin: 2vw;
  height: 20px;
}
.gg img {
  width: 80vw;
  border-radius: 5px;
}
.ant-carousel .slick-slide {
  text-align: center;
  height: 200px;
  background: gray;
  overflow: hidden;
}
.ant-carousel .slick-slide .carousel-img {
  width: auto;
  height: 200px;
  max-width: 100%;
  max-height: 100%;
}
.ant-carousel .slick-slide h3 {
  color: #fff;
}
.vnavbar-container {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 5vw;
  box-sizing: border-box;
  margin: 0;
}
.vnavbar-container .vnavbar-list {
  flex: 1 1;
  list-style: none;
  text-align: center;
}
.vnavbar-container .vnavbar-list a {
  display: block;
  text-align: center;
  text-decoration: none;
  font-size: 22px;
  letter-spacing: 10px;
  padding-left: 10px;
  box-sizing: border-box;
  color: #888;
  width: 50%;
  margin: 0 auto;
}
.vnavbar-container .vnavbar-list .active {
  font-size: 24px;
  border-bottom: 6px solid #c8e8fc;
  color: black;
  font-weight: bold;
  /* transition: all 0.3s; */
}
.fade-enter {
  opacity: 0;
  z-index: 1;
}
.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 250ms ease-in;
  transition: opacity 250ms ease-in;
}
.ant-modal-close {
  top: -50px;
  color: white;
}

.vproductShow-card-container {
  margin: 0;
  padding: 0;
  list-style: none;
}
.vproductShow-card-container .vproductShow-card-cell {
  border-radius: 10px;
  margin: 5vw;
  padding: 5vw 5vw;
  background-color: white;
  box-shadow: 0 5px 10px 0 rgba(174, 174, 174, 0.4);
  display: flex;
  align-items: center;
}
.vproductShow-card-container .vproductShow-card-cell img {
  width: 100%;
}
.vproductShow-card-container .vproductShow-card-cell h2 {
  margin: 0;
}
.vproductShow-card-container .vproductShow-card-cell .infs {
  margin: 5px;
  color: gray;
}
.vproductShow-card-container .vproductShow-card-cell .price-and-pop {
  margin: 1px;
}
.vproductShow-card-container .vproductShow-card-cell .price-and-pop .price {
  font-size: 20px;
  font-weight: bold;
  padding: 3px 10px;
  border-radius: 50px;
  color: rgba(201, 160, 47, 0.9);
  background-color: rgba(255, 231, 166, 0.9);
}
.vproductShow-card-container .vproductShow-card-cell .price-and-pop .pop {
  margin-left: 10px;
  width: 40px;
  height: 18px;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 1px;
  border-radius: 15px;
  color: #999;
  border: 1px solid #999;
}
.vproductShow-card-container .vproductShow-card-cell .button-copy {
  height: 30px;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(0, #ff9000), to(#ff5000));
  background-image: -webkit-linear-gradient(left, #ff9000 0, #ff5000 100%);
  background-image: linear-gradient(to right, #ff9000 0, #ff5000 100%);
  border: none;
  border-radius: 60px;
  font-size: 18px;
  color: white;
  margin: 2px;
  padding: 0px 10px;
}
.vproductShow-card-container .vproductShow-card-cell .button-share {
  height: 30px;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(0, #ff9000), to(#ff5000));
  background-image: -webkit-linear-gradient(left, #ff9000 0, #ff5000 100%);
  background-image: linear-gradient(to right, #ff9000 0, #ff5000 100%);
  border: none;
  border-radius: 60px;
  font-size: 18px;
  color: white;
  margin: 2px;
}
